.gpt3__whatgpt3 {
    display: flex;
    flex-direction: column;

    background: var(--color-footer);
    padding: 2rem;
    /* ff 3.6+ */
    background: -moz-radial-gradient(
        circle at 30% -100%,
        #40135f 25%,
        #5f39a0 100%
    );

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(
        circle at 30% -100%,
        #40135f 25%,
        #5f39a0 100%e
    );

    /* opera 11.10+ */
    background: -o-radial-gradient(
        circle at 30% -100%,
        #40135f 25%,
        #5f39a0 100%
    );

    /* ie 10+ */
    background: -ms-radial-gradient(
        circle at 30% -100%,
        #40135f 25%,
        #5f39a0 100%
    );

    /* global 92%+ browsers support */
    background: radial-gradient(
        circle at 2% -100%,
        #4a4220 40%,
        #635936 90%,
        #595030 100%
    );

    filter: drop-shadow(0px 4px 4px rgba(0, 0, 0, 0.25));
}

.gpt3__whatgpt3-feature {
    display: flex;
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature {
    margin: 1rem;
}

.gpt3__whatgpt3-feature .gpt3__features-container__feature-text {
    max-width: 700px;
}
.gpt3__whatgpt3-heading {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 4rem 1rem 2rem 1rem;
}
.gpt3__whatgpt3-heading h1 {
    font-family: var(--font-family);
    font-size: 34px;
    line-height: 45px;
    font-weight: 800;
    max-width: 510px;
}
.gpt3__whatgpt3-heading p {
    font-family: var(--font-family);
    font-size: 16px;
    line-height: 30px;
    font-weight: 500;
    color: #f3f773;
    cursor: pointer;
    user-select: none;
}
.gpt3__whatgpt3-heading p:hover {
    text-shadow: 0px 0px 5px #000000, 0px 0px 6px #f3f773;
}

.gpt3__whatgpt3-container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
    margin-top: 2rem;
}

.gpt3__whatgpt3-container .gpt3__features-container__feature {
    flex: 1;
    min-width: 190px;
    display: unset;
    flex-direction: column;
}
.gpt3__whatgpt3-container .gpt3__features-container__feature-text {
    margin-top: 0.5rem;
}
@media screen and (max-width: 1054px) {
    .gpt3__whatgpt3 {
        margin: 2rem 3rem 2rem 3em;
    }
}
@media screen and (max-width: 921px) {
    .gpt3__features-container__feature {
        flex-direction: column;
    }

    .gpt3__features-container__feature p {
        margin: 0rem 0 0 0;
    }
    .gpt3__features-container__feature-text {
        margin-top: 0.5rem;
    }
    .gpt3__whatgpt3-heading {
        margin: 2rem 1rem 2rem 1rem;
    }
    .gpt3__whatgpt3-heading {
        flex-direction: column;
        align-items: flex-start;
    }

    .gpt3__whatgpt3-heading p {
        margin: 0.5rem 0 0 0;
    }
}
@media screen and (max-width: 905px) {
}

@media screen and (max-width: 736px) {
    .gpt3__whatgpt3-container {
        flex-direction: column;
        margin: 0 2rem 0 0;
    }
}
@media screen and (max-width: 608px) {
    .gpt3__whatgpt3-heading h1 {
        font-size: 29px;
        line-height: 37px;
    }
}
@media screen and (max-width: 550px) {
    .gpt3__whatgpt3 {
        margin: 1.5rem 1.5rem 2rem 1.5rem;
    }
}

@media screen and (max-width: 450px) {
    .gpt3__whatgpt3-heading h1 {
        font-size: 26px;
        line-height: 35px;
    }
}

