* {
    box-sizing: border-box;
    padding: 0;
    margin: 0;
    scroll-behavior: smooth;
}

body {
    background: var(--color-bg);
}

a {
    color: unset;
    text-decoration: none;
}

.gradient__bg {
    background: -moz-radial-gradient(circle at 5% 25%, #400670 0%, #14051f 30%);

    /* safari 5.1+,chrome 10+ */
    background: -webkit-radial-gradient(
        circle at 5% 25%,
        #400670 0%,
        #14051f 30%
    );

    /* opera 11.10+ */
    background: -o-radial-gradient(circle at 5% 25%, #400670 0%, #14051f 30%);

    /* ie 10+ */
    background: -ms-radial-gradient(circle at 5% 25%, #400670 0%, #14051f 30%);

    /* global 92%+ browsers support */
    background: radial-gradient(
        circle at 0% 0%,
        #706636 0%,
        var(--color-bg) 13%
    );
}

.gradient__text {
    background-image: var(--gradient-text);
    background-clip: text;
    color: transparent;

    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.section__padding {
    padding: 4rem 6rem;
}

.section__margin {
    margin: 4rem 6rem;
}

.scale-up-center {
    -webkit-animation: scale-up-center 0.2s cubic-bezier(0.39, 0.575, 0.565, 1)
        both;
    animation: scale-up-center 0.2s cubic-bezier(0.39, 0.575, 0.565, 1) both;
}

@-webkit-keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}
@keyframes scale-up-center {
    0% {
        -webkit-transform: scale(0.5);
        transform: scale(0.5);
    }
    100% {
        -webkit-transform: scale(1);
        transform: scale(1);
    }
}

@media screen and (max-width: 700px) {
    .section__padding {
        padding: 4rem;
    }
    .section__margin {
        margin: 4rem;
    }
}

@media screen and (max-width: 550px) {
    .section__padding {
        padding: 4rem 2rem;
    }
    .section__margin {
        margin: 4rem 2rem;
    }
}
